const CHANGE_VALUE = 'CHANGE_VALUE';
const SET_ERROR = 'SET_ERROR';
const DELETE_VALUE = 'DELETE_VALUE';
const CHANGE_SEARCH = 'CHANGE_SEARCH';

const PythagorasTheorem = 'PythagorasTheorem';
const PrimeFactorization = 'PrimeFactorization';
const QuadraticEquation = 'QuadraticEquation';
const VERTEX_PARABOLA = 'VERTEX_PARABOLA';
const GreatestCommonDivisor = 'GreatestCommonDivisor';
const LeastCommonMultiple = 'LeastCommonMultiple';
const ScaleNotation = 'ScaleNotation';

let initialState = {
    data: [
        {id: 1, name: 'Pythagoras Theorem', inputs: [
                {name: 'a', type: 'number', value: '', degree: '2'},
                {name: '+', type: 'sign'},
                {name: 'b', type: 'number', value: '', degree: '2'},
                {name: '=', type: 'sign'},
                {name: 'c', type: 'number', value: '', degree: '2'}
            ]},
        {id: 2, name: 'Prime Factorization', inputs: [
                {name: 'N', type: 'number', value: ''},
                {name: '=',  type: 'sign', accept: 'both'},
                {name: 'n', type: 'number', index: '1', degree: 'q 1', accept: 'onlyRead'},
                {name: '* ', type: 'sign',  accept: 'onlyRead'},
                {name: 'n', type: 'number', index: '2', degree: 'q 2', accept: 'onlyRead'},
                {name: '*', type: 'sign', accept: 'onlyRead'},
                {name: '...', type: 'sign',  accept: 'onlyRead'}
            ]},
        {id: 3, name: 'Quadratic Equation',
            inputs: [
                {name: 'a', type: 'number', value: ''},
                {name: 'x', degree: '2',  type: 'sign'}, {name: '+',  type: 'sign'},
                {name: 'b', type: 'number', value: ''},
                {name: 'x', degree: '', type: 'sign'}, {name: '+', type: 'sign'},
                {name: 'c ', type: 'number', value: ''},
                {name: '=', type: 'sign'}, {name: '0', type: 'sign'},
            ],
            result: [{name: 'x', type: 'number'}, {name: '=', type: 'sign'},
                {name: '{}', type: 'sign'}]
        },
        {id: 4, name: 'The Vertex of a Parabola',
            inputs: [
                {name: 'a', type: 'number', value: ''},
                {name: 'x', degree: '2',  type: 'sign'}, {name: '+',  type: 'sign'},
                {name: 'b', type: 'number', value: ''},
                {name: 'x', degree: '', type: 'sign'}, {name: '+', type: 'sign'},
                {name: 'c ', type: 'number', value: ''},
                {name: '=', type: 'sign'}, {name: '0', type: 'sign'},
            ],
            result: [{name: '(', type: 'sign'}, {name: 'X', type: 'number'}, {name: ',', type: 'sign'},
                {name: 'Y', type: 'number'}, {name: ')', type: 'sign'}]
        },
        {id: 5, name: 'Greatest Common Divisor', inputs: [
                {name: '12', type: 'number', value: '420'},  {name: ',', type: 'sign'},
                {name: '36', type: 'number', value: '231'}, {name: '=', type: 'sign'},
                {name: '12',  type: 'number', accept: 'onlyRead'},
            ]},
        {id: 6, name: 'Least Common Multiple', inputs: [
                {name: '6', type: 'number', value: ''},  {name: ',', type: 'sign'},
                {name: '8', type: 'number', value: ''}, {name: '=', type: 'sign'},
                {name: '24', type: 'number', accept: 'onlyRead'}
            ]},
        {id: 7, name: 'Scale of notation (2-16)', inputs: [
                {name: 'a', type: 'number', value: ''},
                {name: '', type: 'index', value: '', index: 'n'},
                {name: '=', type: 'sign'},
                {name: 'b', type: 'number', value: ''},
                {name: ' ', type: 'index', value: '', index: 'p'}
            ]},
    ],
    search: ''
};

const MainReducer = (state = initialState, action) => {
    let elemId; let elem;
    const searchElement = (id) => {
        state.data.some((res, i) => res.id === id && (elemId = i));
        return elemId;
    };
    switch (action.type) {
        case CHANGE_VALUE:
            elem = searchElement(action.id);
            state.data[elem].inputs.some(
                (res) => ((res.name === action.name) && (res.value = action.value)));
            return {...state, data: [...state.data]};
        case SET_ERROR:
            elem = searchElement(action.id);
            state.data[elem].inputs.some(
                (res) => ((res.name === action.name) && (res.error = action.value)));
            return {...state, data: [...state.data]};
        case DELETE_VALUE:
            debugger;
            elem = searchElement(action.id);
            state.data[elem].inputs.map(
                (res) => (res.value = ''));
            state.data[elem].inputs = state.data[elem].inputs.filter(res =>
                (res.accept !== 'onlyWrite'));
            if (action.id === 3) {
                state.data[elem].result[2].name = '{}';
            }
            if (action.id === 4) {
                state.data[elem].result[1].name = 'X';
                state.data[elem].result[3].name = 'Y';
            }
            return {...state, data: [...state.data]};
        case PythagorasTheorem:
            elem = searchElement(1);
            state.data[elem].inputs.some(
                (res) => ((res.name === action.name) && (res.value = action.value)));
            return {...state, data: [...state.data]};
        case PrimeFactorization:
            elem = searchElement(2);
            state.data[elem].inputs = state.data[elem].inputs.filter(res =>
                (res.accept !== 'onlyWrite'));
            action.data.map((res) => state.data[elem].inputs.push(res));
            return {...state, data: [...state.data]};
        case QuadraticEquation:
            elem = searchElement(3);
            state.data[elem].result[2].name = `{${action.data[0]} , ${action.data[1]}}`;
            return {...state, data: [...state.data]};
        case VERTEX_PARABOLA:
            elem = searchElement(4);
            state.data[elem].result[1].name = action.data[0];
            state.data[elem].result[3].name = action.data[1];
            return {...state, data: [...state.data]};
        case GreatestCommonDivisor:
            elem = searchElement(5);
            state.data[elem].inputs.some(
                (res, k) => ((res.accept === 'onlyWrite') &&
                    (state.data[elem].inputs.splice(k, 1))));
            state.data[elem].inputs.push({name: action.value,  type: 'sign', accept: 'onlyWrite'});
            return {...state, data: [...state.data]};
        case LeastCommonMultiple:
            elem = searchElement(6);
            state.data[elem].inputs.some(
                (res, k) => ((res.accept === 'onlyWrite') &&
                    (state.data[elem].inputs.splice(k, 1))));
            state.data[5].inputs.push({name: action.value,  type: 'sign', accept: 'onlyWrite'});
            return {...state, data: [...state.data]};
        case ScaleNotation:
            elem = searchElement(7);
            state.data[elem].inputs.some(
                (res) => ((res.name === action.name) && (res.value = action.value)));
            return {...state, data: [...state.data]};
        case CHANGE_SEARCH:
            return {...state, search: action.value};
        default:
            return state;
    }
};

export const changeSearch = (value) => ({type: CHANGE_SEARCH, value: value});
const changeValue = (id, name, value) => ({type: CHANGE_VALUE, id: id,
    name: name, value: value});
const setError = (id, name, value) => ({type: SET_ERROR, id: id, name: name, value: value});
export const deleteValue = (id) => ({type: DELETE_VALUE, id: id});

export const setPythagorasTheorem = (name, value) => (
    {type: PythagorasTheorem, name: name, value: value});
export const setPrimeFactorization  = (data) => (
    {type: PrimeFactorization, data: data});
export const setGreatestCommonDivisor  = (value) => (
    {type: GreatestCommonDivisor, value: value});
export const setLeastCommonMultiple  = (value) => (
    {type: LeastCommonMultiple, value: value});
export const setQuadraticEquation  = (data) => (
    {type: QuadraticEquation , data: data});
export const setVertexParabola  = (data) => (
    {type: VERTEX_PARABOLA , data: data});
export const setScaleNotation  = (name, value) => (
    {type: ScaleNotation , name: name, value: value});

export const controlValue = (id, name, value) => (dispatch) => {
    if (value) {
        let boolean = true; let q; let point = 0;
        let sumCode = 0;
        if (id !== 7) {
            if (value[0] === '0' && value.length !== 1)
                dispatch(setError(id, name, true));
            else {
                for (let i = 0; i < value.length; i++) {
                    q = value[i].charCodeAt(0);
                    if (((q > 57 || q < 48) && q !== 46) ||
                        (q === 46 && (point === 1 || i === 0))){
                        boolean = false;
                        break;
                    }
                    else if (q === 46) {point = 1}
                }
            }
        } else {
            for (let i = 0; i < value.length; i++) {
                q = value[i].charCodeAt(0);
                if (name === "" || name === " ") {
                    sumCode += q;
                    if ((q === 48 && i === 0) ||
                        (q !== 48 && (q < 49 || q > 57))) {
                        boolean = false;
                        break;
                    }
                }
                else if (q < 48 || (q > 57 && q < 65) || q > 70) {
                    boolean = false;
                    break;
                }
            }
        }
        if (boolean && ((sumCode !== 0 && sumCode < 104) || !sumCode)) {
            dispatch(changeValue(id, name, value));
        }
        else
            dispatch(setError(id, name, true));
    } else {
        dispatch(changeValue(id, name, value));
    }
    setTimeout(() => dispatch(setError(id, name, false)), 200);
};

export default MainReducer;