import React, {Component} from 'react';
import {Forms} from "../Forms/Forms";
import {connect} from "react-redux";
import s from "./main.module.css";
import {
    changeSearch, controlValue, deleteValue,
    setPythagorasTheorem, setPrimeFactorization,
    setGreatestCommonDivisor, setLeastCommonMultiple, setQuadraticEquation, setVertexParabola, setScaleNotation
} from "../../redux/MainReducer";

import notResults from '../../assets/errorAuth.png'
import mathPanda from '../../assets/mathPanda.png'
import {
    getDivisorAndMultiple,
    getPrimeFactorization,
    getPythagorasTheorem,
    getQuadEqAndVerPar, getScaleNotation
} from "../../functions/functions";

const PythagorasTheorem = 'Pythagoras Theorem';
const PrimeFactorization = 'Prime Factorization';
const QuadraticEquation = 'Quadratic Equation';
const GreatestCommonDivisor = 'Greatest Common Divisor';
const LeastCommonMultiple = 'Least Common Multiple';
const VertexParabola = 'The Vertex of a Parabola';
const ScaleNotation = 'Scale of notation (2-16)';

class Main extends Component {

    state = {
        data: null
    };

    componentDidMount() {
        this.setState({data: this.props.data.filter(res =>
                res.name.toLowerCase().includes(this.props.search.toLowerCase()) || this.props.search === '')})
    }
    componentDidUpdate(prevProps, prevState) {
        if (prevProps.search !== this.props.search) {
            this.setState({data: this.props.data.filter(res =>
                    res.name.toLowerCase().includes(this.props.search.toLowerCase()) || this.props.search === '')})
        }
    }

    count = (name, data) => {
        switch (name) {
            case PythagorasTheorem:
                return this.props.setPythagorasTheorem(...getPythagorasTheorem(data));
            case PrimeFactorization:
                return this.props.setPrimeFactorization(getPrimeFactorization(data[0].value));
            case QuadraticEquation:
                return this.props.setQuadraticEquation(getQuadEqAndVerPar(data, 'QuadraticEquation'));
            case GreatestCommonDivisor:
                return this.props.setGreatestCommonDivisor(getDivisorAndMultiple(data, 'Divisor'));
            case LeastCommonMultiple:
                return this.props.setLeastCommonMultiple(getDivisorAndMultiple(data, 'Multiple'));
            case VertexParabola:
                return this.props.setVertexParabola(getQuadEqAndVerPar(data, 'VertexParabola'));
            case ScaleNotation:
                return this.props.setScaleNotation(...getScaleNotation(data));
            default:
                return 0;
        }
    };

    render() {
        return <div className={s.main}>
            <div className={s.divSearch}>
                <img className={s.imgMain} src={mathPanda} alt={'Not Results'}/>
                <input className={s.inputSearch} placeholder={'search...'} value={this.props.search}
                       onChange={(e) => this.props.changeSearch(e.target.value)}/>
            </div>
            {this.state.data && this.state.data.map((res, k) => <div key={k} className={s.divElement}>
                <Forms {...res} id={res.id} count={this.count} controlValue={this.props.controlValue}
                       deleteValue={this.props.deleteValue}/>
            </div>)}
            {this.state.data && this.state.data.length === 0 && <div className={s.notFound}>
                <img className={s.imgResults} src={notResults} alt={'not Results'}/>
                <div>No results found</div>
            </div>}
            <footer className={s.auth}>
                Math Panda<br/>
                Copyright © 2019 Ivan Mayakovsky
            </footer>
        </div>
    }
}

const mapStoreToProps = (store) => {
    return {
        data: store.main.data,
        search: store.main.search
    }
};

const DispatchToProps = {
    controlValue,
    deleteValue,
    setPythagorasTheorem,
    setPrimeFactorization,
    setQuadraticEquation,
    setVertexParabola,
    setGreatestCommonDivisor,
    setLeastCommonMultiple,
    setScaleNotation,
    changeSearch
};

export default connect(mapStoreToProps, DispatchToProps)(Main);
