import React from "react";
import s from "./forms.module.css";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

export const Forms = (props) => {

  return <div className={s.mainForm}>
      <div className={s.name}>{props.name}</div>
      <div className={s.form}>
          {props.inputs.map((res, k) => (
              <div key={k} className={s.formExample}>
                  {res.accept !== 'onlyWrite' && <div>
                          <i>{res.name}</i>
                          {res.index && <sub className={s.degree}>&nbsp;{res.index}</sub>}
                          {res.index && !res.degree && <i>&nbsp;</i>}
                          {res.degree && <sup>&nbsp;{res.degree}&nbsp;</sup>}
                          <i>&nbsp;</i>
                  </div>}
              </div>
          ))}
      </div>
      <div className={s.form2}>
          {props.inputs.map((res, k) => (
              <div key={k} className={s.formInputs}>
                  {res.accept !== 'onlyRead' && <div>
                      {(res.type === 'number') &&
                      <input className={s.input + " " + (res.error && s.error)} value={res.value}
                             onChange={(e) => props.controlValue(props.id, res.name, e.target.value)}
                             placeholder={res.name}
                             onKeyDown={(e) => e.key === 'Enter' && props.count(props.name, props.inputs)}/>}
                      {(res.type === 'index') && <sub className={s.degree}>
                          <input className={s.inputIndex + " " + (res.error && s.error)} value={res.value}
                                 onChange={(e) => props.controlValue(props.id, res.name, e.target.value)}
                                 placeholder={res.index} />
                      </sub>}
                      {(res.type === 'onlyWrite') && <i>{res.name}</i>}
                      {res.type === 'sign' && <i>{res.name}</i>}&nbsp;
                      {res.index && res.type !== 'index' && <sub className={s.degree}>{res.index}</sub>}
                      {res.degree && <sup className={s.degree}>{res.degree}&nbsp;</sup>}
                  </div>}
              </div>
          ))}
      </div>
      {props.result && <div className={s.form3}>
          {props.result.map((res, k) => (
              <div key={k} className={s.formInputs}>
                      <i>{res.name}</i>&nbsp;
                      {res.index && res.type !== 'index' && <sub className={s.degree}>{res.index}</sub>}
                      {res.degree && <sup className={s.degree}>{res.degree}&nbsp;</sup>}
              </div>
          ))}
      </div>}

      <div className={s.buttons}>
        <FontAwesomeIcon icon={'caret-square-right'} className={s.buttonGo}
                         onClick={() => props.count(props.name, props.inputs)}/>
        <FontAwesomeIcon icon={'redo'} className={s.buttonReset} onClick={() => props.deleteValue(props.id)}/>
      </div>
  </div>
};