
export const getPythagorasTheorem = (inputs) => {
    let result = null;
    let name = '';
    if (!inputs[0].value) {
        result = inputs[4].value*inputs[4].value - inputs[2].value*inputs[2].value;
        name = 'a';
    }
    else if (!inputs[2].value) {
        result = inputs[4].value*inputs[4].value - inputs[0].value*inputs[0].value;
        name = 'b' ;
    }
    else {
        result = inputs[2].value*inputs[2].value + inputs[0].value*inputs[0].value;
        name = 'c';
    }
    return [name, Math.sqrt(result)];
};

let data = [];

let sanna = (n, kara) => {
    let prime = true;
    for (let i = 2; i <= Math.sqrt(n); i++) {
        if (n % i === 0) {
            prime = false;
            let a = n / i;
            let b = i;
            sanna(a, kara);
            sanna(b, kara);
            break;
        }
    }
    if (prime) {
        kara(n);
    }
};

export const getPrimeFactorization = (number) => {
    let karaPrimeFactorization = (n) => {
        let bool = data.some((res) => ((res.name === `${n}`) && (res.degree++)));
        !bool && data.push({name: `${n}`, degree: 1, type: 'sign', accept: 'onlyWrite'},
            {name: `*`, type: 'sign', accept: 'onlyWrite'});
    };
    sanna(number, karaPrimeFactorization);
    let dataPrimeFactorization = data;
    dataPrimeFactorization.splice(dataPrimeFactorization.length - 1, 1);
    data = [];
    return dataPrimeFactorization;
};

export const getQuadEqAndVerPar  = (inputs, name)  => {
    let [a, b, c] = [inputs[0].value, inputs[3].value, inputs[6].value];
    a === '' && (a = 1); b === '' && (b = 1); c === '' && (c = 0);
    if (name === 'QuadraticEquation')
        return(
            [(-b + Math.sqrt(b*b - 4*a*c)) / (4*a*c), (-b - Math.sqrt(b*b - 4*a*c)) / (4*a*c)]);
    else {
        let x = (-b) / (2*a);
        return [x, (a*x*x + b*x + c)];
    }
};

export const getDivisorAndMultiple = (inputs, name) => {
    let a = 0; let b = 0; let c;
    if (inputs[0].value > inputs[2].value) {
        a = inputs[0].value; b = inputs[2].value;
    } else {
        a = inputs[2].value; b = inputs[0].value;
    }
    if (name === 'Divisor') {
        debugger;
        if (a === '0') {
            c = b;
        } else if (b === '0') {
            c = a;
        } else if (a % b === 0) {
            c = b;
        } else {
            let j;
            while (b !== 0) {
                j = b;
                b = a % b;
                a = j;
            }
            c = a;
        }
    } else {
        c = b;
        let arrA = []; let arrB = [];
        let karaLeastCommonMultiple = (n) => {
            data = [...data, n];
        };
        sanna(a, karaLeastCommonMultiple);
        arrA = [...data]; data = [];
        sanna(b, karaLeastCommonMultiple);
        arrB = [...data]; data = [];
        for (let i = 0; i < arrA.length; i++) {
            let bool = false;
            for (let j = 0; j < arrB.length; j++) {
                if (arrA[i] === arrB[j]) {
                    bool = true;
                    arrB[j] = 0;
                    break;
                }
            }
            if (!bool)
                (c *= arrA[i]);
        }
    }
    return(c);
};
export const getScaleNotation = (inputs)  => {
    let [a, n, b, p] = [inputs[0].value, inputs[1].value, inputs[3].value, inputs[4].value];
    let name; let count; let elem;
    if (!a){ name = 'a'; a = b; count = true} else if (!n){name = ''; n = p; count = false}
    else if(!p){name = ' '; count = false} else {name = 'b'; count = true}
    let ten = 0; let number = '';
    if (count) {
        if (n !== '10') {
            for (let i = 0; i < a.length; i++) {
                if (a[i].charCodeAt(0) >= 65) {
                    if (n < 10) {
                        return [name, 'NAN'];
                    } else {
                        elem = a[i].charCodeAt(0) - 55;
                    }
                } else {
                    elem = a[i];
                }
                ten += elem*Math.pow(n, a.length - 1 - i);
            }
        } else {
            ten = a;
        }
        if (p !== '10') {
            while (ten >= 1) {
                number = ten % p + number;
                ten = Math.floor(ten / p);
            }
        } else {
            return [name, ten];
        }
    } else {

    }
    return [name, number];
};